@font-face {
  font-family: "Magnat Head";
  src: url("/assets/fonts/subset-Magnat-HeadMedium.woff2") format("woff2"), url("/assets/fonts/subset-Magnat-HeadMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gaultier";
  src: url("/assets/fonts/subset-Gaultier-Regular.woff2") format("woff2"), url("/assets/fonts/subset-Gaultier-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gaultier";
  src: url("/assets/fonts/subset-Gaultier-Bold.woff2") format("woff2"), url("/assets/fonts/subset-Gaultier-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gaultier";
  src: url("/assets/fonts/Gaultier-RegularItalic.woff2") format("woff2"), url("/assets/fonts/Gaultier-RegularItalic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}
:root {
  --color-3: #145d55;
}

html {
  font-family: "Gaultier", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", "SongTi TC", sans-serif;
  background: #fff;
  color: #424242;
}

body {
  padding: 2em;
}

header {
  margin: 2rem auto;
  animation: fadeIn;
  animation-duration: 1s;
}

main {
  padding: 0;
  margin: 0 auto;
  font-size: 1em;
}

header p {
  font-style: italic;
}

header p, h1 {
  text-align: center;
}

h1 {
  font-family: "Magnat Head", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", "SongTi TC", sans-serif;
  font-size: 2em;
  font-weight: 500;
  text-transform: none;
  letter-spacing: 0.02em;
}

p + img {
  margin-top: 2em;
}

img {
  margin-left: auto;
  margin-right: auto;
}

header img {
  margin-bottom: 2rem;
}

#intro section + section {
  margin-top: 1em;
}

#info {
  font-style: italic;
}
#info p + p {
  margin-top: 0;
}

#process {
  padding: 2rem;
  background: no-repeat url("/assets/images/kajo/waves-background.jpg");
  background-color: #477577;
  color: #fff;
}
#process p {
  max-width: 50em;
}

#logo-o {
  shape-outside: circle();
  width: 12em;
  margin: 2em auto;
}

#palette img + img {
  margin-top: 2rem;
}

#color-palette {
  max-width: 60%;
}

footer {
  margin-top: 5em;
}

#outcomes figure {
  max-width: 25em;
  position: relative;
  margin: 0 auto;
}
#outcomes figcaption {
  background-color: #FCC262;
  padding: 1em 1.5em;
  font-weight: normal;
  position: absolute;
  bottom: 1em;
  right: -1em;
}

@media (min-width: 1150px) {
  header, main {
    max-width: 40rem;
  }

  #intro {
    display: flex;
    gap: 2rem;
  }
  #intro section {
    padding: 0;
  }
  #intro section + section {
    margin-top: 0;
  }

  #blurb {
    width: 50%;
  }

  #logo-o {
    float: right;
    margin: 1em;
  }

  #process {
    padding: 2rem 4rem;
  }

  #font-palette {
    max-width: 30rem;
  }

  #color-palette {
    max-width: 15rem;
  }

  #outcomes {
    display: flex;
    gap: 2rem;
  }
  #outcomes div {
    margin-top: 5rem;
    max-width: 50%;
  }
  #outcomes figcaption {
    top: 2em;
    right: -13em;
    bottom: unset;
  }
}